import { HANDSOME,NOHANDSOME,NOHANDSOME_PENDING,NOHANDSOME_FULFILLED,NOHANDSOME_REJECTED } from "../constants";


const add = (state=100,action={})=>{
  switch(action.type){
    case NOHANDSOME:
      //case
      console.log("i am handosome")
      return "fuck"

    case NOHANDSOME_PENDING:
      // console.log("pending")
      console.log(action)//Pengdingactiontype,payload
      return "pengding" + state


    case NOHANDSOME_FULFILLED:
      console.log(action)
      return action.payload.data.info.seed
      return 1 //casereturnstate,returnstate

    case NOHANDSOME_REJECTED:
      console.log(1111111111111)
      console.log(action)
      return "reject"


    case HANDSOME:
      return state + action.add
    default:
      return state
  }
}
export default  add


